
.header {
  position: relative;
  overflow: hidden;
  color: #fff;
  background-color: rgba(7,27,17,0.5);
}
.header .content-wrapper {
  position: relative;
  padding: 24px 12px 18px 24px;
  font-size: 0;
}
.header .content-wrapper .avatar {
  display: inline-block;
}
.header .content-wrapper .content {
  display: inline-block;
  margin-left: 16px;
  vertical-align: top;
}
.header .content-wrapper .content .title {
  margin: 2px 0 8px 0;
}
.header .content-wrapper .content .title .brand {
  display: inline-block;
  vertical-align: top;
  width: 30px;
  height: 18px;
  background-image: url("brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-devive-pixel-ratio: 3) {
.header .content-wrapper .content .title .brand {
    background-image: url("brand@3x.png");
}
}
.header .content-wrapper .content .title .name {
  margin-left: 6px;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
}
.header .content-wrapper .content .description {
  font-size: 12px;
  line-height: 12px;
  margin-bottom: 10px;
}
.header .content-wrapper .content .support .text {
  font-size: 10px;
  line-height: 14px;
}
.header .content-wrapper .support-count {
  position: absolute;
  right: 12px;
  bottom: 15px;
  height: 24px;
  line-height: 24px;
  padding: 0px 8px;
  background-color: rgba(0,0,0,0.2);
  border-radius: 14px;
  text-align: center;
}
.header .content-wrapper .support-count:active {
  background-color: rgba(206,198,198,0.2);
}
.header .content-wrapper .support-count .count {
  margin-right: 2px;
  margin-left: 2px;
  vertical-align: top;
  font-size: 10px;
  line-height: 26px;
}
.header .content-wrapper .support-count .icon-keyboard_arrow_right {
  font-size: 10px;
  line-height: 24px;
}
.header .bulletin-wrapper {
  position: relative;
  padding: 0px 16px 0 22px;
  height: 28px;
  line-height: 28px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background-color: rgba(7,17,27,0.2);
}
.header .bulletin-wrapper:active {
  background-color: rgba(206,198,198,0.2);
}
.header .bulletin-wrapper .bulletin-title {
  display: inline-block;
  width: 22px;
  height: 12px;
  background-image: url("bulletin@2x.png");
  background-size: 22px 12px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-devive-pixel-ratio: 3) {
.header .bulletin-wrapper .bulletin-title {
    background-image: url("bulletin@3x.png");
}
}
.header .bulletin-wrapper .bulletin-text {
  margin: 0 30px 0 4px;
  vertical-align: middle;
  font-size: 10px;
}
.header .bulletin-wrapper .icon-keyboard_arrow_right {
  position: absolute;
  right: 12px;
  top: 9px;
  font-size: 10px;
}
.header .background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: -1;
  overflow: hidden;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .fade-enter,
.header .fade-leave-to {
  opacity: 0;
}
.header .fade-enter-active,
.header .fade-leave-active {
  transition: all 0.3s ease;
}
.header .detail {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(7,17,27,0.8);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.header .detail .detail-wrapper {
  min-height: 100%;
  width: 100%;
}
.header .detail .detail-wrapper .detail-main {
  margin-top: 64px;
  padding-bottom: 64px;
}
.header .detail .detail-wrapper .detail-main .name {
  font-size: 16px;
  font-weight: 700;
  line-height: 16px;
  text-align: center;
}
.header .detail .detail-wrapper .detail-main .star-wrapper {
  margin-top: 16px;
  padding: 2px 0;
  text-align: center;
}
.header .detail .detail-wrapper .detail-main .support {
  width: 80%;
  margin: 0 auto;
}
.header .detail .detail-wrapper .detail-main .support .support-item {
  padding: 0 12px;
  margin-bottom: 12px;
  font-size: 0;
}
.header .detail .detail-wrapper .detail-main .support .support-item:last-child {
  margin-bottom: 0;
}
.header .detail .detail-wrapper .detail-main .support .support-item .text {
  line-height: 16px;
  font-size: 12px;
}
.header .detail .detail-wrapper .detail-main .bulletin {
  width: 80%;
  margin: 0 auto;
}
.header .detail .detail-wrapper .detail-main .bulletin .content {
  padding: 0 12px;
  line-height: 24px;
  font-size: 12px;
}
.header .detail .detail-close {
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;
  clear: both;
  font-size: 32px;
}


